/* src/App.css */
/* 既存のCSSの末尾に追加 */
.timer-settings {
  margin: 2rem 0;
  width: 80%;
  max-width: 500px;
}

.timing-options, .duration-options {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  margin: 1rem 0;
}

.timing-option, .duration-option {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
}

.timing-option:hover, .duration-option:hover {
  background: rgba(255, 255, 255, 0.2);
}

h2, h3 {
  margin: 1rem 0;
}
/* 既存のCSSに追加 */
.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.disabled input {
  cursor: not-allowed;
}

.state-display {
  margin: 1rem 0;
  padding: 0.5rem;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
}

.state-display p {
  margin: 0;
  font-size: 0.9rem;
  color: #fff;
}

